    import {mapState, mapMutations} from 'vuex';
	import {getInfo,getDyn} from '../../api/api.js';

    export default {
		data() {
			return {
				userInfo: '',
				dynInfo: '',
				statusInfo: '',
				uid: ''
			}
		},
        methods: {
			getUserInfo() {
				getInfo(this.uid).then(data => {
					this.userInfo = data;
				});
				getDyn(this.uid).then(data => {
					this.dynInfo = data.resp;
					// console.log(this.dynInfo);
				});
			}
        },
		onLoad(option) {
			// console.log(option.uid);
			this.uid = option.uid;
		},
		onShow() {
			this.getUserInfo();
		}
    }
